// app/search/page.jsx
import Link from "next/link";
import { shopifyRequest } from "../../utils/shopify";
import { SEARCH_PRODUCTS } from "../../queries/search";
import { calculateFinalPrice } from "../../utils/price";
import { formatIndianCurrency } from "../../utils/formatIndianCurrency";

// Revalidate every 30 minutes for search results
export const revalidate = 1800;

async function calculateProductPrice(description, selectedKarat = "10K") {
  try {
    const diamondShapeMatch = description.match(
      /Diamond Shape:\s*([A-Za-z,\s]+?)(?=Total Diamonds|Diamond Weight|$)/i
    );
    const totalDiamondsMatch = description.match(
      /Total Diamonds:\s*([\d,\s]+?)(?=Diamond Weight|Total Diamond Weight|Metal Weights|$)/i
    );
    const diamondWeightMatch = description.match(
      /Diamond Weight:\s*([\d.,\s]+?)(?=Total Diamond Weight|Metal Weights|$)/i
    );
    const goldWeightMatch = description.match(
      new RegExp(`${selectedKarat} Gold:\\s*([\\d.]+)g`, "i")
    );

    const diamondShapes = diamondShapeMatch
      ? diamondShapeMatch[1]
          .trim()
          .split(",")
          .map((s) => s.trim())
          .filter((s) => s)
      : [];

    const diamondCounts = totalDiamondsMatch
      ? totalDiamondsMatch[1]
          .trim()
          .split(",")
          .map((s) => s.trim())
          .filter((s) => s && !isNaN(s))
          .map((c) => parseInt(c))
      : [];

    const diamondWeights = diamondWeightMatch
      ? diamondWeightMatch[1]
          .trim()
          .split(",")
          .map((s) => s.trim())
          .filter((s) => s && !isNaN(s))
          .map((w) => parseFloat(w))
      : [];

    const goldWeight = goldWeightMatch ? parseFloat(goldWeightMatch[1]) : 0;

    const diamonds = diamondShapes.map((shape, i) => ({
      shape,
      weight: diamondWeights[i] || 0,
      count: diamondCounts[i] || 0,
    }));

    const result = await calculateFinalPrice({
      diamonds,
      goldWeight,
      goldKarat: selectedKarat,
    });

    return result.totalPrice;
  } catch (error) {
    console.error("Error calculating price:", error);
    return 0;
  }
}

async function transformSearchResults(productsEdges) {
  const transformedProducts = await Promise.all(
    productsEdges.map(async ({ node: product }) => {
      const calculatedPrice = await calculateProductPrice(
        product.description || "",
        "10K"
      );

      return {
        id: product.id,
        handle: product.handle,
        title: product.title,
        price: calculatedPrice,
        image: product.featuredImage?.url || "/placeholder.jpg",
      };
    })
  );

  return transformedProducts;
}

export default async function SearchResultsPage({ searchParams }) {
  const query = searchParams.q || "";

  if (!query) {
    return (
      <div className="max-w-7xl mx-auto px-4 md:px-8 py-20">
        <div className="text-center py-24">
          <h1 className="text-3xl md:text-4xl font-bold mb-4">
            Search Products
          </h1>
          <p className="text-gray-600 mb-6">
            Enter a search term using the search bar above
          </p>
          <Link
            href="/rings"
            className="inline-block bg-black text-white px-6 py-3 rounded-full hover:bg-gray-800 transition-all duration-300"
          >
            Browse All Rings
          </Link>
        </div>
      </div>
    );
  }

  try {
    const response = await shopifyRequest(SEARCH_PRODUCTS, {
      query,
      first: 50,
    });

    const results = response.data?.products?.edges || [];
    const transformedResults = await transformSearchResults(results);

    return (
      <div className="max-w-7xl mx-auto px-4 md:px-8 py-20">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-3xl md:text-4xl font-bold mb-2">
            Search Results for "{query}"
          </h1>
          <p className="text-gray-600">
            {transformedResults.length}{" "}
            {transformedResults.length === 1 ? "product" : "products"} found
          </p>
        </div>

        {/* Results Grid */}
        {transformedResults.length > 0 ? (
          <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 gap-6">
            {transformedResults.map((product) => (
              <Link
                key={product.id}
                href={`/product/${product.handle}`}
                className="group"
              >
                <div className="bg-white border border-gray-200 rounded-2xl overflow-hidden shadow-sm hover:shadow-lg transition-all duration-300">
                  <div className="relative aspect-square overflow-hidden bg-gray-50">
                    <img
                      src={product.image}
                      alt={product.title}
                      className="w-full h-full object-cover transform group-hover:scale-105 transition-transform duration-300"
                    />
                    <div className="absolute inset-0 bg-black/0 group-hover:bg-black/5 transition-colors"></div>
                  </div>

                  <div className="p-4">
                    <h3 className="font-medium text-gray-800 text-base md:text-lg line-clamp-2 mb-2">
                      {product.title}
                    </h3>

                    {/* Price Display */}
                    {product.price > 0 && (
                      <div className="mt-2">
                        <p className="text-xs text-gray-500 mb-1">
                          Starting from (10K Gold)
                        </p>
                        <p className="text-lg font-bold text-[#0a1833]">
                          ₹{formatIndianCurrency(product.price)}
                        </p>
                      </div>
                    )}
                  </div>
                </div>
              </Link>
            ))}
          </div>
        ) : (
          <div className="text-center py-24">
            <p className="text-xl text-gray-700 mb-3">
              No products found for "{query}"
            </p>
            <p className="text-gray-500 mb-6">
              Try different keywords or browse our collections
            </p>
            <Link
              href="/rings"
              className="inline-block bg-black text-white px-6 py-3 rounded-full hover:bg-gray-800 transition-all duration-300"
            >
              Browse All Rings
            </Link>
          </div>
        )}
      </div>
    );
  } catch (error) {
    console.error("Search error:", error);
    return (
      <div className="max-w-7xl mx-auto px-4 md:px-8 py-20">
        <div className="text-center py-24">
          <p className="text-xl text-red-600 mb-3">
            An error occurred while searching
          </p>
          <p className="text-gray-500 mb-6">Please try again later</p>
          <Link
            href="/rings"
            className="inline-block bg-black text-white px-6 py-3 rounded-full hover:bg-gray-800 transition-all duration-300"
          >
            Browse All Rings
          </Link>
        </div>
      </div>
    );
  }
}
